html, body{
  width: 100%;
  height: 100%;
  position: relative;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans',
    sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-stroke: 0px!important;-webkit-font-smoothing: antialiased !important;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App, #root{
  height: 100%;
  width: 100%;
}

h1, h2{
  margin: 0;
}


h1{
  font-family: 'Bellefair', serif;
  font-weight: 400;
  font-size: 110px;
  letter-spacing: -6px;
  text-align: center;
  color: #000;
  margin-top: 160px;
}

h2{
  color: #484848;
  font-weight: 300;
  font-size: 24px;
  margin-top: -10px;
}

h5{
  font-weight: 300;
  font-size: 18px;
}

a{
  font-weight: 700;
  color: #484848;
  border-bottom: 2px solid #484848;
  text-decoration: none;
  transition: .3s ease-in-out;
}

a:hover{
  color: #e0e0e0;
  border-bottom: 2px solid #e0e0e0;
}

.h-100{
  height: 100%;
  display: block;
  width: 100%;
}

p{
  font-weight: 300;
  line-height: 150%;
  width: 40%;
  margin: 0 auto;
  font-size: 18px;
  text-align: left;
  margin-bottom: 50px;
}


.frame{
  width: 100%;
  height: 600px;
  z-index: -9999;
}

footer{
  width: 40%;
  margin: 0 auto;
  line-height: 150%;
  margin-bottom: 50px;
}

.w-40{
  margin-left: 10%;
  width: 80%;
  position: absolute;
  z-index: -9;
  margin-top: -150px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

